import React, {ChangeEvent, DetailedHTMLProps, FC, KeyboardEvent, memo, TextareaHTMLAttributes} from 'react'
import style from './SuperTextarea.module.scss'


type DefaultTextareaPropsType = DetailedHTMLProps<TextareaHTMLAttributes<HTMLTextAreaElement>,HTMLTextAreaElement>


type SuperTextareaPropsType = DefaultTextareaPropsType & { //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void

    spanClassName?: string

}

const SuperTextarea: FC<SuperTextareaPropsType> = memo((
    {
       //          
        onChange, onChangeText,
        onKeyPress, onEnter,

        className,
        ...restProps//       restProps
    }
) => {

    const finalTextareaClassName = `${style.textarea} ${className} `

    const onChangeCallback = (e: ChangeEvent<HTMLTextAreaElement>) => {
        onChange //    onChange
        && onChange(e) //     ( onChange  )
        onChangeText && onChangeText(e.currentTarget.value)

    }

    const onKeyPressCallback = (e: KeyboardEvent<HTMLTextAreaElement>) => {
        onKeyPress && onKeyPress(e);
        onEnter //    onEnter
        && e.key === 'Enter' //     Enter
        && onEnter() //   
    }

    return (
        <>
            <textarea
                onChange={onChangeCallback}
                onKeyPress={onKeyPressCallback}
                className={finalTextareaClassName}
                {...restProps}
            />

        </>
    )
})

export default SuperTextarea